<script>
    export default {
        name: "Layout",
        data() {
            return {
                activeIndex: '1',
                language: 'ch',
                uri_icon_lang_switch: require("../../static/index/icon_index_language_switch.png"),
                uri_icon_index_business_phone: require("../../static/index/icon_index_business_phone.png"),
                uri_icon_index_business_email: require("../../static/index/icon_index_business_email.png"),
                scroll: '',
                timer: '',
                seen: true,
                currentMenu:'首页'
            };
        },
        created() {
            this.language = sessionStorage.getItem('language');
            sessionStorage.setItem('language', '');
            if (this.language == null || this.language == '') {
                this.language = 'ch';
            }
        },
        methods: {
            languageSwitch() {
                if (this.language == 'en') {
                    this.language = 'ch';
                    this.$router.push({name: "Index"});
                } else {
                    this.language = 'en';
                    this.$router.push({name: "IndexE"});
                }
            },
            handleSelect() {
                if (this.language == 'ch') {
                    this.$router.push({name: "Index"});
                } else {
                    this.$router.push({name: "IndexE"});
                }
            },
            handleSelectMobile() {
                if (this.language == 'ch') {
                    this.$router.push({name: "Index"});
                } else {
                    this.$router.push({name: "IndexE"});
                }
            },
            handleScroll() {
                this.scroll = document.documentElement.scrollTop + document.body.scrollTop; //获取滚动后的高度
                if (this.scroll > 600) {  //判断滚动后高度超过600px,就显示
                    this.$refs.btn.style.display = 'block';
                } else {
                    this.$refs.btn.style.display = 'none';
                }
            },
            backTop() {
                // 设置滚动行为改为平滑的滚动
                window.scrollTo({
                    top: 0,
                    behavior: "smooth"
                });
            },
            menuSeen() {
                // 屏幕尺寸
                let screenHeight = document.body.clientWidth;
                if (screenHeight > 415) {
                    this.seen = true;
                } else {
                    this.seen = false;
                }
            },
            logoClick() {
                this.$router.push({name: "Index"});
                this.language = 'ch';
            }
        },
        mounted() {
            this.menuSeen();
            window.addEventListener('scroll', this.handleScroll);
        },
    }
</script>

